/*======= Old Site CSS ==== */

/* Author profiles */
.author .archive-header {
	margin-bottom: 24px;
}
.author-info {
	border-top: 1px solid #ededed;
	margin: 24px 0;
	padding-top: 24px;
	overflow: hidden;
}
.author-description p {
	color: #757575;
	font-size: 13px;
	line-height: 1.846153846;
}
.author.archive .author-info {
	border-top: 0;
	margin: 0 0 48px;
}
.author.archive .author-avatar {
	margin-top: 0;
}


/* Social */
.social-wrapper {
	width: 100%;
	position: relative;	
	overflow: hidden;
}
#sociallinks {
	position: relative;
	float: right;
	z-index: 6;	
	margin: 0 0px 0 0;
}

#sociallinks  ul {
	margin: 0px;
	padding: 0px;
	list-style-type: none;
	z-index: 2;
	position: relative;
}
#sociallinks  ul li {
	display: inline-block;
	margin: 0 0 0 5px;
}
#sociallinks  ul a {
	text-decoration: none;
	display: block;
	height: 30px;
	position: relative;
	width:30px;
	font-size: 28px;
	text-align: center;
}
/* = Services
-------------------------------------------------------------- */
.service {
	width: 100%;
	float: left;
	margin: 0 0 30px 0;
	overflow: hidden;
}
h2.redsubtitle {
	color: #FF001B;
	
}
h1.news-title {
	color: #FF001B;
	text-transform:uppercase;
	margin: 10px 0 20px 10px;
	
}
.newpush {
	margin: 0px 0 0px 10px;
}
.short-descr {
	width: 100%;
	padding: 0 0 2px 0;
	margin: 0 0 10px 0;
	font-style: italic;
	color: #999;	
	/*font-family: 'Oranienbaum', serif;*/
	font-family: 'Crimson Text', serif;
	font-weight: 400italic;
	border-bottom: 1px solid #FF001B;
	font-size: 18px;
}
.service-image {
	width: 100%;
	margin: 0 0 15px 0;
}
.service-image img {
	width: 100%;
}	
.service-description {
	width: auto;
}
.sideservice {	
	ul {
		list-style: none;
		padding: 0 0;
		margin: 0 0;
	}
}

.sideservice ul li {
	width: auto;
	margin: 0 0 15px 0;	
}
.sideservice ul li a {
	display: block;
	background-color: #eee;
	color: #6C6C6C !important;
	border-left: 4px solid #eee;
}
.sideservice ul li a:hover {
	border-left: 4px solid #FF001B;
}
.sideservice ul li h2 {
	
	font-size: 16px;
	border-bottom: 1px solid #9f9f9f;
	padding : 0 0 2px 0;	
	margin: 0 0 5px 0;
}
.sideservice ul li p {
	font-style: italic;
	margin: 0px !important;
	/*font-family: 'Oranienbaum', serif;*/
	font-family: 'Crimson Text', serif;
	font-weight: 400italic;
	font-size: 16px;
}
.spad a {
	padding: 10px;
}

/* Banner */
section[role="banner"] {
	margin-bottom: 48px;
}

/* Sidebar */
.widget-area .widget, .widget-archives .widget {
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	hyphens: auto;
	margin: 0px 0 48px 0;
	word-wrap: break-word;
}
.widget-area .widget h3, .widget-archives .widget h3 {
	margin-bottom: 24px;
}
.widget-area .widget p,
.widget-area .widget li,
.widget-area .widget .textwidget,
.widget-archives .widget p,
.widget-archives .widget li,
.widget-archives .widget .textwidget {
	font-size: 13px;
	line-height: 1.4;
}
.widget-area .widget p, .widget-archives .widget p {
	margin-bottom: 24px;
}
.widget-area .textwidget ul, .widget-archives .textwidget ul {
	list-style: disc outside;
	margin: 0 0 24px;
}
.widget-area .textwidget li, .widget-archives .textwidget li {
	margin: 0 0 0px 36px;
}
.widget-area  li, .widget-archives  li {
	margin: 0 0 10px 0px;
}
.widget-area .widget a, .widget-archives .widget a {
	color: #757575;
	text-decoration: none;
}
.widget-area .widget a:hover, .widget-archives .widget a:hover {
	color: #21759b;
}
.widget-area #s, .widget-archives #s {
	width: 53.66666666666%; /* define a width to avoid dropping a wider submit button */
}
/* =======================================================================
						  		Single Project View
======================================================================= */
.subnav {
	width: 100%;
	margin-bottom: 10px;
	margin-top: 5px;
	overflow: hidden;
	text-align: center;
}
#nosubnav {
	width: 100%;
	margin-bottom: 20px;
	margin-top: 10px;
	border-bottom: 1px solid #FF001B;
	overflow: hidden;
}
.subnav ul.cat-menu,
.subnav div.cat-menu > ul {
	display: none;
}
.subnav ul.cat-menu.toggled-on {
	display: block;
	li {
		display: block;
		width: 100%;
		a {
			padding: 8px 10px 4px;
		}
	}
	@media screen and (min-width: $tabwidth) {
		display: inline-block;
		width: auto;
	}
}
.subnav ul {
	margin: 0px;
	padding: 0px;
}
.subnav li {
	text-align: center;
	width: auto;
	display: block;
	/*margin-right: 25px;*/
}
.subnav li a{
	display: block;
	padding: 13px;
	text-decoration:none;
	color: #FF001B;
	font-weight:bold;
	font-size: 14px;
	text-transform: uppercase;
}
.subnav li a:hover, .subnav li a.active {
	color: #fff;
	background-color: #FF001B;
	
}
/*  
	Portfolio Structure  
*/
.portfolio-content {
	width: 100%;
	margin: 30px 0 0 0;
	border-top: 1px solid #ff001b;
	padding: 10px 0 0 0;	
}
.image-wrapper {
