}
.port-post-div a, .viewall-div a {
	width: 100%;
	position: relative;
	overflow: hidden;
	opacity: 1;
	text-decoration: none;
	color: #666;
	display: block;
}
.port-post-div a:hover, .viewall-div a:hover {
	opacity: 1;
	color: #FF001B;
}
#proj-page-left {
	width: 100%;
	float:left;
	padding-left: 15px;
	padding-right: 15px;
	color: #666;
	line-height: 1.6;
	box-sizing: border-box;
}
#proj-page-left p {
	margin: 0 0 20px 0;
}
.proj-title h2 {
	font-size: 14px;
	font-family: Georgia, "Times New Roman", Times, serif;
	font-weight: normal;
	line-height: 1.4;
	color: #666;
}
h1.main-proj-title {
	font-size: 22px;
	/*font-family: Georgia, "Times New Roman", Times, serif;*/
	font-weight: normal;
	line-height: 1.4;
	margin: 0 0 5px 0;
}
.sec-proj-title {
	font-size: 16px;
	font-family: Georgia, "Times New Roman", Times, serif;
	margin: 0 0 0px 0;
