}
.proj-pagi-prev,
.proj-pagi-next {
	width: 49%;
}
.proj-pagi-prev {
	float: left;
}
.proj-pagi-next {
	float: right;
}
.proj-pagi-next a {
	width:auto;
	float: right;
	margin-top: 10px;
	margin-bottom: 20px;
	text-decoration:none;
	color:#FE0000;
}
.proj-pagi-prev a {
	width:auto;
	float: left;
	margin-top: 10px;
	margin-bottom: 20px;
		text-decoration:none;
	color:#FE0000;
}
span.plain-text  {
	color: #666;	
}
.gallery-wrapper,
.gallery-wrapper .flexslider {
	width: 100%;
	float: left;
	position: relative;
	margin: 0 0;
}
.gallery-wrapper {
	margin-top: 10px;
}
.gallery-wrapper .flexslider {
	border: none;
}
.flexslider.largeview {
	width: 100%;
}
.flexslider.largeview img {
	display: block;
    width: 100%;
    height: 100%;
}
.flexslider.largeview li {
	margin: 0 0!important;
}
.flexslider ul.slides {
	margin: 0 0;
	padding: 0 0;
}
#flexslider2.flexslider {
	width: 100%;
	margin: 0 0 10px;
	overflow: hidden;
}
#flexslider2.flexslider li {
	height: 430px;
}
#flexcarousel.flexslider ul {
	margin-right: -5px;
	margin-left: -5px;
	box-sizing: border-box;
}
#flexcarousel.flexslider li {
	opacity: 0.5;
	margin-right: 0!important;
	margin-bottom: 10px;
	padding-left: 5px;
	padding-right: 5px;
	box-sizing: border-box;
	width: 16.66666%;
}
#flexcarousel.flexslider li.flex-active-slide {
	opacity: 1;
}
#flexslider2.flexslider,
#flexcarousel.flexslider {
	border-radius: 0;
}
#flexcarousel.flexslider {
	margin-bottom: 10px;
}
#flexslider2.flexslider li .imagediv {
	background-position: top center;
	background-size: cover;
	background-repeat: no-repeat;
	height: 100%;
	width: 100%;
}
.flex-direction-nav {
	z-index: 10;
}
#single-proj-right.rightdiv {
	box-sizing: border-box;
	width: 400px;
	float: none;
	margin: 0 auto;
	padding-right: 0;
}
#flexcarousel.flexslider li img {
	height: 100%;
	width: 100%;
	position: relative;
	top: 0;
	left: 0;
}
#flexslider2 .flex-direction-nav a {
	margin: 0 0 0;
	top: 0;
	width: 120px;
	height: 100%;
	font-size:0px;
	color: transparent;
}
#flexslider2 .flex-direction-nav .flex-disabled {
	opacity: 0.3!important;
	z-index: 2!important;
}
#flexslider2 .flex-direction-nav a:before {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
}
#flexslider2 .flex-direction-nav a.flex-next:before {
	right: 0;
}
#flexslider2 .flex-direction-nav a.flex-prev:before {
	left: 0;
}
#flexslider2.flexslider,
#flexslider2.flexslider li {
	height: 250px;
}
#flexcarousel.flexslider li {
	cursor: pointer;
	display: inline-block;
	float: left;
}
#flexcarousel.flexslider li.active-slide {
	opacity: 1;
}
#cboxOverlay {
	background-image:none;
