			}
		}
	}

	.thumbnail, 
	.thumbnail img,
	.project-info {
		width: 100%;
		height: auto;
		float: left;
		margin: 0 0;
	}

	.thumbnail {
		display: block;
		background-size:cover;
		background-repeat: no-repeat;
		background-position: center;
		width: 100%;
		height: auto;
		@media screen and (min-width: $tabwidth) {
			height: 220px;
		}
		@media screen and (min-width: $midwidth) {
			height: 230px;
		}
		@media screen and (min-width: $deskwidth) {
			height: 260px;
		}
		img {
			display: block;
			@media screen and (min-width: $tabwidth) {
				display: none;
			}
		}
	}
	.editpost {
		display: inline-block;
		position: absolute;
		bottom: 0;
		right: 15px;
		text-transform: uppercase;
		text-decoration: none;
		z-index: 10;
		font-size: 12px;
		font-weight: 600;
		line-height: 1.2;
		color: #FFF;
		padding: 5px 8px 3px;
	    background: #8b8b8b;
	}
}

.subnav .menu-toggle {
	cursor: pointer;
	font-weight: 600;
	font-size: 16px;
	margin: 5px 0;
	position: relative;
	span.arrow {
		display: block;
		width: 0;
		height: 0;
		position: absolute;
		top: 9px;
    	right: 92px;
		border-top: 6px solid $color__text-main;
   		border-left: 5px solid transparent; 
   		border-right: 5px solid transparent; 
   		@include transition(all ease-out .3s);
	}
}

.site-footer {
	a {
		color: #FFF;
		text-decoration: none;
		@include transition(all ease-out .3s);
		&:hover {
			opacity: 0.4;
		}
	}
	.wrapper {
		padding-top: 20px;
		padding-bottom: 14px;
		@media screen and (min-width: $tabwidth) {
			padding-top: 14px;
		}
	}
	.flexrow {
		@media screen and (min-width: $tabwidth) {
			@include flexbox-align-items(center);
		}
	}
	.flexcol {
		text-align: center;
		@media screen and (min-width: $tabwidth) {
			width: 49.9%;
			float: left;
			text-align: left;
		}
	}
	.social-media {
		margin: 0 0;
		padding: 0 0;
		list-style: none;
		display: inline-block;
		@media screen and (min-width: $tabwidth) {
			float: right;
		}
		li {
			display: inline-block;
			float: left;
		}
		a {
			display: block;
			color: #FFF;
			padding: 4px 8px;
			font-size: 20px;
			line-height: 1;
		}
	}
	.footer-site-info {
		color: #FFF;
		font-size: 13px;
		margin-bottom: 10px;
		@media screen and (min-width: $tabwidth) {
			margin-bottom: 0;
		}
	}
}


